package com.parkjin.blog.pages

import androidx.compose.runtime.*
import com.varabyte.kobweb.core.*
import com.varabyte.kobwebx.markdown.*

@Page
@Composable
fun RxJavaZipPage() {
    CompositionLocalProvider(LocalMarkdownContext provides MarkdownContext("RxJavaZip.md", mapOf("category" to listOf("개발"), "title" to listOf("RxJava Zip 올바른 병렬처리"), "description" to listOf("RxJava Zip 올바른 병렬처리에 대하여"), "createdAt" to listOf("2022.04.17"), "tags" to listOf("Android", "RxJava")))) {
        com.parkjin.blog.components.layout.PostPageLayout {
            org.jetbrains.compose.web.dom.H2(attrs = { id("operator-zip") }) {
                org.jetbrains.compose.web.dom.Text("Operator Zip")
