            org.jetbrains.compose.web.dom.P {
                org.jetbrains.compose.web.dom.Text("RxJava를 사용하면서 대부분 ")
                org.jetbrains.compose.web.dom.B {
                    org.jetbrains.compose.web.dom.Text("여러 개의 스트림을 동시에 호출")
