            org.jetbrains.compose.web.dom.Pre { org.jetbrains.compose.web.dom.Code { org.jetbrains.compose.web.dom.Text("""val firstWord = Observable.fromCallable { getFirstWord() }
	.subscribeOn(Schedulers.io())
val lastWord = Observable.fromCallable { getLastWord() }
	.subscribeOn(Schedulers.io())

Observable.zip(firstWord, lastWord) { first, last ->
	"${'$'}first ${'$'}last!"
}.observeOn(AndroidSchedulers.mainThread())
	.subscribe { Log.e("result word", it) }
""") } }
            com.varabyte.kobweb.compose.dom.GenericTag("img", "src=\"/images/rx_java_zip/result_good.png\" alt=\"database\" width=\"80%\"")
            org.jetbrains.compose.web.dom.P {
