                    org.jetbrains.compose.web.dom.Text("동시에 호출")
                }
                org.jetbrains.compose.web.dom.Text("되는 것을 확인 할 수 있다.")
                org.jetbrains.compose.web.dom.Br()
                org.jetbrains.compose.web.dom.Text("위 코드에서는 Zip에서 큰 역할을 하고 있지 않아 따로 스레드를 지정해주고 있지 않지만, Zip의 역할이 커진다면 스레드를 지정해주는 것이 좋다.")
            }
            org.jetbrains.compose.web.dom.H2(attrs = { id("끝내며") }) {
