                org.jetbrains.compose.web.dom.Text("는 사용자가 getContentIfNotHandled() 함수와 peekContent() 함수를 선택하여 명시적으로 사용할 수 있다는 장점을 가지고 있습니다.")
            }
            org.jetbrains.compose.web.dom.P {
